            <div class="recent-information-room-deposit">{{recentData.deposit}}</div>
            <div class="recent-information-floor">{{recentData.fllor}}</div>
            <div class="recent-information-content">{{recentData.content}}</div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
