          deposit: " 300/40",
          floor: "2 17",
          content: "",
          heart: true
        },
        {
          img: require("../../assets/room3.jpg"),
          type: "",
          deposit: " 3",
          floor: "2 32",
          content: "     3 2 ",
          heart: false
        },
        {
          img: require("../../assets/room6.jpg"),
          type: "",
          deposit: " 300/38",
